<template>
	<div class="login">
		<div class="name">蔬菜种植管理系统</div>
		<div class="title">登录</div>
		<el-form :model="loginForm" ref="loginForm" label-width="80px">
			<el-form-item label="用户名" prop="phone" :rules="rules.phone">
				<el-input placeholder="请输入用户名" class="form-field" v-model="loginForm.phone"></el-input>
			</el-form-item>
			<el-form-item label="密码" prop="password" :rules="rules.password">
				<el-input placeholder="请输入密码" class="form-field" v-model="loginForm.password" type="password"></el-input>
			</el-form-item>
		</el-form>
		<el-button class="btn" type="primary" @click="handleLogin">登录</el-button>

		<div class="footer">
			没有账号？
			<router-link to="/register">点击注册</router-link>
		</div>
	</div>
</template>
<script>
	import services from "@/services";
	import { mapActions } from "vuex";
	export default {
		name: "Login",
		data() {
			return {
				loginForm: {
					phone: "",
					password: ""
				},

				rules: {
					phone: [{ required: true, message: "请输入手机号", trigger: "blur" }],
					password: [{ required: true, message: "请输入密码", trigger: "blur" }]
				}
			};
		},

		methods: {
      ...mapActions(['actionLogin']),
			handleLogin() {
				this.$refs["loginForm"].validate(valid => {
					if (!valid) {
						return;
					}
					services
						.login(this.loginForm)
						.then(res => {
							if (res.data.code !== 0) {
								this.$message.error("密码或用户名错误");
							} else {
								this.actionLogin(res.data.data);
								this.$message.success("登录成功");
								this.$router.push("/home");
							}
						})
						.catch(err => {
							this.$message.error(err);
						});
				});
			}
		}
	};
</script>
<style lang="scss" scoped>
	.login {
		width: 400px;
		margin: 20vh auto 0;
		text-align: center;
	}

	.name {
		font-size: 28px;
		margin-bottom: 50px;
		color: rgba(0, 0, 0, 0.45);
	}

	.title {
		font-weight: bold;
		font-size: 16px;
		margin-bottom: 20px;
	}

	.form-field {
		width: 300px;
	}

	.btn {
		@extend .form-field;
		margin-left: 80px;
	}

	.footer {
		margin-top: 30px;
		padding-top: 20px;
		border-top: 1px solid #ccc;
	}
</style>